
.projects-div {
    display: flex;
    flex-direction: row;
  }
  
  .hidden {
    display: none;
    visibility: hidden;
  }

  
  .project-image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .project-text-area {
    position: absolute;
    left: calc(70vw);
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    row-gap: 1rem;
    width: 26vw;
  }
  
  .hidden {
    display: none;
  }
  
  .scroll-down {
    position: absolute;
    bottom: 2rem;
    text-align: center;
    width: 100%;
  }
  
  .project-name {
    margin: 0;
  }
  
  .image-section {
    height: 100vh;
    display: flex;
    align-items: center;
    position: relative;
    scroll-snap-align: center;
    perspective: 500px;
  }

  .project-section {
    height: 100vh;
    scroll-snap-align: center;
    perspective: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow-x: auto;
    
  }

  .projects-list::-webkit-scrollbar {
    display: none;
  }

  .projects-list {
    display: flex;
    list-style: none;
    overflow-x: scroll;
    column-gap: 2vw;
    padding: 0;
  }

  .project-div:last-of-type {
    margin-right: 4vw;
  }
  
  html {
    scroll-snap-type: y mandatory;
  }
  
  
  @media only screen and (max-width: 768px) {
    .image-div {
          width: 50vw;
          aspect-ratio: 3/4;
    }
  
    .project-text-area {
      position: relative;
      left: 0;
      top: calc(10vh);
      row-gap: 0rem;
      width: 92vw;
      row-gap: 0.6rem;
      text-align: center;
    }
  
  
    .image-section {
      flex-direction: column;
      justify-content: center;
    }
  
    .project-name {
      font-size: 1.5rem;
    }
    
    .project-skill {
      font-size: 1rem;
    }
  }